import axios from "axios"
import { Message } from 'element-ui'

// axios.defaults.baseURL = 'http://localhost:8080'
// axios.defaults.headers.common["Authorization"] = "hahah"

// axios
var instance =  axios.create({
    baseURL: 'https://nobibibi.top/', // 
    // baseURL: 'http://localhost:8888/', // 
    // timeout: 50000, // 1
    // headers: {'Authorization': 'hehe'}, // 
    withCredentials: true, // cookie header
})



// 
instance.interceptors.request.use(function (config) {
    // ,config
    if (localStorage.getItem("token") != null){
        config.headers['token'] = localStorage.getItem("token");
    }
    return config
}, function (error) {
    // ,
    return Promise.reject(error)
})


// 
instance.interceptors.response.use(function (response) {
    // , response
    // 
    if (response.status && response.status == 200){
        // token,Headers
        if (response.headers['auth'] != null){
            localStorage.setItem("token", response.headers['auth'])
        }

        // (token) 
        if (response.data.code == 21037){
            Message.error(response.data.msg)
            localStorage.clear()
            location.replace("/")
            return ;
        }
        //   
        if (response.data.code == 0 || (response.data.code >= 10000 && response.data.code < 20000) || response.data.code == 1000){
            Message.success(response.data.msg)
            return response;
        }else if(response.data.code > 20000){ // 
            Message.error(response.data.msg)
            return ;
        }else{
            Message.info(response.data.msg)
            return ;
        }
        return ;

    }else{
        // 
        Message.error("")
        return ;
    }
    return response;
}, function (error) {
    // ,404,403,500
    Message.error("")
    return ;
})

export default instance

